import { Box, Button, Flex, Heading, Image, Text } from "@chakra-ui/react";
import React from "react";

export const MoreAbout = () => {
  return (
    <Flex
      flexDirection={["column", "column", "column", "row-reverse"]}
      pt={[5, 10]}
      pb={[50, 100]}
      px={[4, 8, 8, 12]}
      justify="center"
      alignItems="center"
      bg={"#f4f4f4"}
    >
      {/* Image Section */}
      <Box
        w={["100%", "100%", "100%", "40%"]}
        textAlign={["center", "center", "center", "left"]}
        mr={[0, 0, 0, 10]}
        mb={[5, 5, 5, 0]}
      >
        <Image
          boxSize={["85%", "60%", "60%", "100%"]}
          src="two-beautiful-muslim-woman-stretching-exercising-gym-together_8595-31780.jpg"
          alt="coaching"
          mx="auto"
          borderRadius="md"
          objectFit="cover"
        />
      </Box>

      {/* Text Section */}
      <Box
        w={["100%", "100%", "90%", "50%"]}
        fontFamily="inter"
        textAlign={["center", "center", "center", "left"]}
      >
        <Heading
          fontSize={["32px", "36px", "42px"]}
          pb={3}
          color="#2d2d2d"
        >
           Propos de Nous
        </Heading>

        <Text
          fontFamily="sans-serif"
          color="gray"
          pt={5}
          pb={5}
          textAlign={["center", "center", "center", "left"]}
          w={["100%", "100%", "90%", "85%"]}
          fontSize={["sm", "md", "lg"]}
        >
          My Coach est une plateforme innovante conue pour combler le foss entre les passionns de fitness et les coachs sportifs certifis. Nous comprenons qu'atteindre vos objectifs de fitness peut tre un dfi sans les conseils et le soutien appropris. C'est pourquoi nous avons cr un espace convivial o vous pouvez facilement vous connecter avec des entraneurs professionnels qui adaptent leur expertise  vos besoins et  votre emploi du temps unique.
        </Text>

        <Flex
          pt={5}
          justify={["center", "center", "flex-start"]}
          flexDirection={["column", "row"]}
          alignItems="center"
        >
          {/* Action Buttons (If Needed Later) */}
          {/* <Button
            fontFamily="inter"
            h={42}
            w={["80%", "180px"]}
            borderRadius={0}
            bg="transparent"
            border="1px solid"
            color="black"
            fontWeight="bold"
            alignSelf="center"
          >
            Rejoignez Nous!
          </Button> */}
        </Flex>
      </Box>
    </Flex>
  );
};
